.Search {
  margin: auto; }
  div .Search {
    position: relative;
    width: 300px;
    padding-bottom: 10px; }
  .Search .icon {
    position: absolute;
    top: 0;
    right: -55px; }
  .Search input {
    border: 0;
    border-bottom: 1px solid gray;
    font-size: 24px;
    outline: none;
    padding: 10px;
    padding-right: 55px;
    -webkit-transition: width 2s;
    -o-transition: width 2s;
    transition: width 2s;
    width: 100%; }



